import BaseRpcRepository from "../../../../../../../packages/bundles/rpc/repositories/base/BaseRpcRepository";

export default class UpdatePasswordRepository extends BaseRpcRepository {

    async updatePassword(body) {
        let requestEntity = {
            method: 'updatePassword.update',
            body: body,
        };

        try {
            let responseEntity = await this.sendRequest(requestEntity);
            return responseEntity.body;
        } catch (error) {
            throw error;
        }
    }
}
