import {Field, Formik} from "formik";
import {Button, Col, Form, FormGroup, Row} from "react-bootstrap";
import React from "react";
import FormErrorWidget from "../../../../../../packages/ext/form/widgets/FormErrorWidget";
import CopyHelper from "../../../../../../packages/ext/browser/helpers/CopyHelper";
import FormHelper from "../../../../../../packages/ext/form/helpers/FormHelper";

export default function PasswordGeneratorForm(props) {
    // const {t} = useTranslation();

    let initialValues = {
        masterKey: '',
        host: '',
        login: '',
        nonce: 1,
        passwordLength: 10,
        password: '',
    };
    initialValues = FormHelper.mergeDefaultValues(props.entity, initialValues);

    let validate = function (values) {
        const errors = {};
        if (!values.host) {
            errors["host"] = 'Required';
        }
        if (!values.masterKey) {
            errors["masterKey"] = 'Required';
        }
        if (!values.login) {
            errors["login"] = 'Required';
        }
        if (!values.nonce) {
            errors["nonce"] = 'Required';
        }
        if (!values.passwordLength) {
            errors["passwordLength"] = 'Required';
        }
        return errors;
    };

    return (
        <Formik
            initialValues={initialValues}
            onSubmit={props.onSubmit}
            validate={validate}
        >
            {({
                  values,
                  errors,
                  // touched,
                  handleChange,
                  handleBlur,
                  handleSubmit,
                  isSubmitting,
              }) => (
                <Form onSubmit={handleSubmit}>


                    <FormGroup>
                        <Row>
                            <Col>
                                <label htmlFor="">
                                    Host
                                </label>
                                <Form.Control
                                    name="host"
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.host}
                                />
                                <FormErrorWidget errors={errors} name='host'/>
                            </Col>
                            <Col>
                                <label htmlFor="">
                                    Login
                                </label>
                                <Form.Control
                                    name="login"
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.login}
                                />
                                <FormErrorWidget errors={errors} name='login'/>
                            </Col>
                        </Row>
                    </FormGroup>

                    <FormGroup>
                        <Row>
                            <Col>
                                <label htmlFor="">
                                    Password length
                                </label>
                                <Form.Control
                                    name="passwordLength"
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.passwordLength}
                                    type="number"
                                />
                                <FormErrorWidget errors={errors} name='passwordLength'/>
                            </Col>
                            <Col>
                                <label htmlFor="">
                                    Nonce
                                </label>
                                <Form.Control
                                    name="nonce"
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.nonce}
                                    type="number"
                                />
                                <FormErrorWidget errors={errors} name='nonce'/>
                            </Col>
                        </Row>
                    </FormGroup>

                    <FormGroup>
                        <Row>
                            <Col>
                                <label htmlFor="">
                                    Master key
                                </label>
                                <Form.Control
                                    name="masterKey"
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.masterKey}
                                    type="password"
                                    autoComplete="off"
                                />
                                <FormErrorWidget errors={errors} name='masterKey'/>
                            </Col>
                            <Col>
                                <label htmlFor="">
                                    Password
                                </label>
                                <div className="input-group">
                                    <Form.Control
                                        id="password"
                                        name="password"
                                        onChange={handleChange}
                                        onBlur={handleBlur}
                                        value={values.password}
                                        readOnly={true}
                                        autoComplete="off"
                                        style={{fontFamily: 'monospace'}}
                                    />
                                    <div className="input-group-append">
                                        <Button data-target="password" onClick={CopyHelper.copy}>Copy</Button>
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    </FormGroup>

                    <Button type="submit" disabled={isSubmitting}>
                        Generate
                    </Button>

                </Form>
            )}
        </Formik>
    );
};
