import TokenEntity from "../../../../../../packages/bundles/user/security/domain/entities/token/TokenEntity";
import CryptoKeyHelper from "../../../../key/domain/helpers/CryptoKeyHelper";

export default class TokenHelper {

    static forgeTokenEntity(keyEntity) {
        let identityEntity = this._forgeIdentityEntity(keyEntity);
        let token = this._forgeTokenString(keyEntity);
        let tokenEntity = new TokenEntity(token, identityEntity);
        // tokenEntity.identity = identityEntity;
        return tokenEntity;
    }

    static _forgeTokenString(keyEntity) {
        let publicKeyHex = keyEntity.publicKey.toString('hex');
        return 'crypto ' + publicKeyHex;
    }

    static _forgeIdentityEntity(keyEntity) {
        let hash160 = CryptoKeyHelper.hashByPublicKey(keyEntity.publicKey);
        return {
            "id": hash160.toString('hex'),
            "statusId": 100,
            "username": keyEntity.address,
            "roles": [
                "rCryptoUser"
            ],
        };
    }
}
