import React from "react";
import {connect} from "react-redux";
import BaseComponent from "../../../../ext/react/base/BaseComponent";
import ModalView from "./views/modalView";
import modalFacade from "../../domain/facades/modalFacade";
import _ from "lodash";

class ModalContainerWidget extends BaseComponent {

    render() {
        let confirmState1 = this.props["modalItem"];

        if(!confirmState1) {
            return '';
        }

        let confirmState = _.clone(confirmState1);

        if (confirmState1.onConfirm) {
            confirmState.onConfirm = () => {
                modalFacade.close();
                confirmState1.onConfirm();
            };
        }

        confirmState.onHide = () => {
            modalFacade.close();
            if (typeof confirmState1.onHide === 'function') {
                confirmState1.onHide();
            }
        };

        return (
            <ModalView {...confirmState}/>
        );
    }
}

const mapStateToProps = (store) => {
    return {
        modalItem: store.modalItem,
    };
};

export default connect(mapStateToProps)(ModalContainerWidget);
