import HashHelper from "../../../../../../packages/ext/hash/helpers/HashHelper";
import ConvHelper from "../../../../../../packages/ext/binary/helpers/ConvHelper";
import _ from 'lodash';
import Sha256Hasher from "../../../../../../packages/ext/hash/libs/encoders/hash/Sha256Hasher";
import EncodeInterface from "../../../../../../packages/core/contract/encoders/EncodeInterface";

/**
 *  
 *
 *    
 *
 * :
 *  -  
 *  -   
 *
 * :
 *  -  
 *  -  
 */
export default abstract class BaseChainHash implements EncodeInterface {

    protected cost: number;
    protected hasher: EncodeInterface;

    constructor(cost: number, hasher: EncodeInterface = null) {
        if (cost < 1) {
            throw new Error('Cost not can be less then 1');
        }
        this.cost = cost;
        if (_.isEmpty(hasher)) {
            hasher = new Sha256Hasher();
        }
        this.hasher = hasher;
    }

    encode(sourceValue) {
        let valueBytes = this.prepareValue(sourceValue);
        let hashBytes;
        let scope = valueBytes;
        for (let nonce = 0; nonce < this.cost; nonce++) {
            hashBytes = this.generateHash(scope);
            scope = this.forHash(valueBytes, hashBytes, scope, nonce);
        }
        return hashBytes;
    }

    protected prepareValue(sourceValue) {
        return ConvHelper.toBuffer(sourceValue);
    }

    protected generateHash(scope) {
        return this.hasher.encode(scope);
        // return HashHelper.hash(this.algorithm, scope);
    }

    protected abstract forHash(valueBytes, hashBytes, scope, nonce: number);
}
