
import TokenEntity from "../../../../../../packages/bundles/user/security/domain/entities/token/TokenEntity";
import container from "../../../../../../packages/core/container/singletons/container";

export default class AuthService {

    protected authRpcRepository;
    protected userProvider;

    constructor(authRpcRepository/*, userProvider*/) {
        this.authRpcRepository = authRpcRepository;
        // this.userProvider = userProvider;
    }

    async authByForm(form) {
        try {
            let value = await this.authRpcRepository.getTokenByForm(form);
            let tokenEntity = new TokenEntity(value.token, value.identity);
            // tokenEntity.identity = value.identity;
            container.get('security.services.userProvider').login(tokenEntity);
            return tokenEntity;
        } catch (error) {
            throw error;
        }
    }

    /*logout() {
        container.get('security.services.userProvider').logout();
    }*/
}
