import React, {Component} from 'react';
import PasswordGeneratorForm from "../views/password/PasswordGeneratorForm";
import passwordGeneratorFactory
    from "../../../../../packages/bundles/passwordGenerator/factories/passwordGeneratorFactory";
import HelpModalLink from "../../../../../packages/bundles/help/web/widgets/modalLink/HelpModalLink";
import {Button} from "react-bootstrap";
import HelpPageLink from "../../../../../packages/bundles/help/web/widgets/pageLink/HelpPageLink";

export default class Password extends Component {

    handleSubmit(values, {setSubmitting}) {
        // let passwordGenerator = new PasswordGenerator(values.masterKey, 10);
        let passwordGenerator = passwordGeneratorFactory.createInstance18kPbkdf2(values.masterKey, values.passwordLength);
        values.password = passwordGenerator.generate(values.host, values.login, values.nonce);
        setSubmitting(false);
    }

    render() {
        let values = {
            masterKey: '',
            host: '',
            login: '',
            nonce: 1,
            passwordLength: 10,
            password: '',
        };
        return (
            <div>
                <HelpPageLink name='utils.password.index'>
                    <Button variant="link">?</Button>
                </HelpPageLink>
                <PasswordGeneratorForm onSubmit={this.handleSubmit} values={values}/>
            </div>
        )
    }
}
