import DocumentFormatInterface from "../../interfaces/DocumentFormatInterface";
import ConvHelper from "../../../../../../packages/ext/binary/helpers/ConvHelper";
import DocumentTypeEnum from "../../../../transport/domain/enums/DocumentTypeEnum";
import PemEncoder from "../encoders/PemEncoder";

export default class DocumentFormat implements DocumentFormatInterface {

    private coinName;

    constructor(coinName) {
        this.coinName = coinName;
    }

    encode(address: string, sig, message: string): string {
        Buffer.isBuffer(sig) && (sig = sig.toString('base64'));
        return [
            '-----BEGIN ' + this.coinName + ' SIGNED MESSAGE-----',
            message,
            '-----BEGIN SIGNATURE-----',
            //'Version: ' + version,
            // 'Address: ' + address,
            sig,
            '-----END ' + this.coinName + ' SIGNED MESSAGE-----'
        ].join('\n')
    }

    decode(document: string) {
        let res = this.parse(document);
        return {
            signature: ConvHelper.toBuffer(res['signature'], 'base64'),
            message: res['signed_message'],
        };
    }

    getType(document: string) {
        let res = this.parse(document);
        if (res.hasOwnProperty('encrypted_message')) {
            return DocumentTypeEnum.ENCRYPTED;
        } else if (res.hasOwnProperty('signed_message')) {
            return DocumentTypeEnum.SIGNED;
        }
        throw new Error('Not supported document type!');
    }


    parse(document: string) {
        let pemEncoder = new PemEncoder();
        let decoded = pemEncoder.decode(document);
        let result = {};
        for (let name in decoded) {
            let value = decoded[name];
            let newName = name.replace(this.coinName.toLowerCase() + '_', '');
            result[newName] = value;
        }
        return result;
    }
}
