import UpdatePasswordRepository from "../repositories/rpc/UpdatePasswordRepository";

export default class UpdatePasswordService {

    protected rpcRepository;

    constructor(rpcRepository: UpdatePasswordRepository) {
        this.rpcRepository = rpcRepository;
    }

    async updatePassword(form) {
        try {
            await this.rpcRepository.updatePassword(form);
            // this.storageRepository.setRequestForm(null);
        } catch (error) {
            throw error;
        }
    }
}
