import BaseCrudService from "../../../../../packages/core/domain/services/BaseCrudService";
import Query from "../../../../../packages/core/domain/libs/Query";

export default class OrderService extends BaseCrudService {

    async kanban(query: Query = null): Promise<any> {
        try {
            let dataProvider = await this.repository.kanban(query);
            if(this.stateRepository) {
                this.stateRepository.setDataProvider(dataProvider);
            }
            return dataProvider;
        } catch (error) {
            throw error;
        }
    }
}
