import RegistrationApiRepository from "../repositories/rpc/RegistrationApiRepository";
import RegistrationStorageRepository from "../repositories/localStorage/RegistrationRequestFormRepository";

export default class RegistrationService {

    protected registrationApiRepository;
    protected registrationStorageRepository;

    constructor(registrationApiRepository: RegistrationApiRepository, registrationStorageRepository: RegistrationStorageRepository) {
        this.registrationApiRepository = registrationApiRepository;
        this.registrationStorageRepository = registrationStorageRepository;

    }

    async requestActivationCode(form) {
        try {
            await this.registrationApiRepository.requestActivationCode(form);
            this.registrationStorageRepository.setRequestForm(form);
        } catch (error) {
            throw error;
        }
    }

    async createAccount(form) {
        try {
            await this.registrationApiRepository.createAccount(form);
            this.registrationStorageRepository.setRequestForm(null);
        } catch (error) {
            throw error;
        }
    }

    getRequestForm() {
        return this.registrationStorageRepository.getRequestForm();
    }
}
