import DocumentService from "../../../../../packages/bundles/crypt/services/DocumentService";
import SessionService from "../../../../../packages/bundles/e2ee/domain/services/SessionService";
import container from "../../../../../packages/core/container/singletons/container";
// import HandShakeRepository from "../../../../packages/e2ee/domain/repositories/p2p/HandShakeRepository";

export default abstract class BaseP2pController {

    protected documentService: DocumentService;
    protected sessionService: SessionService;
    // protected handShakeRepository: HandShakeRepository;

    constructor(
        documentService: DocumentService = null,
        sessionService: SessionService = null
    ) {
        if(documentService == null) {
            documentService = container.get('crypt.services.document');
        }
        if(sessionService == null) {
            sessionService = container.get('e2ee.services.session');
        }
        this.documentService = documentService;
        this.sessionService = sessionService;

        // this.handShakeRepository = container.get('e2ee.repositories.storage.handShake');

    }

    protected send(messageData, address) {
        return this.documentService.send(messageData, address);
    }
}
