import Pbkdf2ChainHash from "../../../../../encode/domain/libs/chainHash/Pbkdf2ChainHash";
import BaseLoginKey from "./BaseLoginKey";

export default class Pbkdf2LoginKey extends BaseLoginKey {

    cost: number = 9000;

    constructor() {
        super();
        let hasher = new Pbkdf2ChainHash(this.cost);
        this.setHasher(hasher);
    }
}
