import BaseCrudService from "../../../../../packages/core/domain/services/BaseCrudService";
import container from "../../../../../packages/core/container/singletons/container";
import Query from "../../../../../packages/core/domain/libs/Query";
import eventEmitter from "../../../../../packages/core/event/singletons/eventEmitter";
import MessengerEventEnum from "../enums/MessengerEventEnum";

export default class MessageService extends BaseCrudService {

    close() {
        container.get('messenger.repositories.state.message').setChatId(null);
    }

    async allByChatId(chatId) {
        let query = {
            filter: {
                chatId: null
            }
        }; // new Query();
        query.filter.chatId = chatId;
        this.all(query);
    }

    async all(query = null) {
        super.all(query);
        let chatId = query.filter.chatId;
        container.get('messenger.repositories.state.message').setChatId(chatId);
    }

    async send(form = null) {
        //form.chatId = container.messenger.repositories.state.message.getChatId();
        try {
            await this.repository.send(form);
        } catch (error) {
            // throw error;
        }
    }

    async updateList() {
        let chatId = container.get('messenger.repositories.state.message').getChatId();
        this.allByChatId(chatId);
    }

    async newMessage(data = null) {
        let chatId = container.get('messenger.repositories.state.message').getChatId();
        eventEmitter.emit(MessengerEventEnum.NEW_MESSAGE, data);
        if (chatId === data.chatId) {
            this.updateList();
            // console.log(chatId, data.chatId);
        }
    }

    getChatId() {
        return this.stateRepository.getChatId();
    }
}
