import diConfigurator from "../../../../../../packages/core/container/singletons/diConfigurator";
import RpcRestorePasswordRepository from "../repositories/rpc/RestorePasswordRepository";
import RpcUpdatePasswordRepository from "../repositories/rpc/UpdatePasswordRepository";
import StorageRestorePasswordRepository from "../repositories/localStorage/RestorePasswordRepository";
import RestorePasswordService from "../services/RestorePasswordService";
import UpdatePasswordService from "../services/UpdatePasswordService";

export default function configureDi(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    bundleDiConfigurator.singleton("repositories.api.restorePassword", RpcRestorePasswordRepository);
    bundleDiConfigurator.singleton("repositories.storage.restorePassword", StorageRestorePasswordRepository);
    bundleDiConfigurator.bind(
        "services.restorePassword",
        RestorePasswordService,
        [
            "this.repositories.api.restorePassword",
            "this.repositories.storage.restorePassword",
        ]
    );


    bundleDiConfigurator.singleton("repositories.api.updatePassword", RpcUpdatePasswordRepository);
    bundleDiConfigurator.bind(
        "services.updatePassword",
        UpdatePasswordService,
        [
            "this.repositories.api.updatePassword",
        ]
    );
}
