import CryptoHashHelper from "../../../key/domain/helpers/CryptoHashHelper";
import HashHelper from "../../../../../packages/ext/hash/helpers/HashHelper";
import format from "./fork/format";
import ecdsa from "ecdsa";
import BigInt from "bigi";
import coins from "coinstring";
import {MessageBuilder as Binary} from "crypto-binary";

export default class CoinMsg {

    ecdsa;
    coinname = 'BITCOIN';
    curve = 'secp256k1';
    magicPrefix;

    constructor(opt = null) {
        this.ecdsa = ecdsa(opt.curve || 'secp256k1');
        this.coinname = opt.coinname;
        this.magicPrefix = new Buffer(opt.magicPrefix);
    }

    magicHash(message) {
        Buffer.isBuffer(message) || (message = new Buffer(message, 'utf8'));
        let bin = new Binary();
        bin.put(this.magicPrefix);
        bin.putVarInt(message.length);
        bin.put(message);
        return CryptoHashHelper.sha256x2(bin.raw());
    }

    sign(privateKey, message, compressed = null) {
        let hash = this.magicHash(message);
        let e = BigInt.fromBuffer(hash);
        let sig = this.ecdsa.sign(hash, privateKey);
        let Q = this.ecdsa.ecparams.g.multiply(BigInt.fromBuffer(privateKey));
        let i = this.ecdsa.calcPubKeyRecoveryParam(e, sig, Q);
        return this.ecdsa.serializeSigCompact(sig, i, (compressed == null || compressed))
    }

    verify(expected, sig, message) {
        Buffer.isBuffer(sig) || (sig = new Buffer(sig, 'base64'));
        let publicKey = this.recoverPubKey(sig, message);
        if (Buffer.isBuffer(expected)) {
            return expected.toString('hex') === publicKey.toString('hex');
        } else {
            let networkVer = coins.decode(expected)[0];
            let actualAddress = coins.encode(HashHelper.ripemd160(publicKey), networkVer);
            return expected === actualAddress
        }
    }

    recoverPubKey(sig, message) {
        Buffer.isBuffer(sig) || (sig = new Buffer(sig, 'base64'));
        let hash = this.magicHash(message);
        let e = BigInt.fromBuffer(hash);
        let parsed = this.ecdsa.parseSigCompact(sig);
        let Q = this.ecdsa.recoverPubKey(e, parsed.signature, parsed.i);
        return Q.getEncoded(parsed.compressed);
    }

    format(address, sig, message) {
        return format(this.coinname, address, sig, message);
    }
}
