import HDKey from 'hdkey';
import CryptoKeyHelper from "../helpers/CryptoKeyHelper";
import ObjectHelper from "../../../../../packages/ext/object/helpers/ObjectHelper";

export default class HdKey {

    hdKey;
    encrypt = {
        isEnabled: false,
        password: null,
    };
    indexEntityPattern = {
        purpose: 44,
        coin: 0,
        account: 0,
        chain: 0,
        address: 0,
    };
    seedBuffer;
    entropy;

    constructor(seedBuffer) {
        this.seedBuffer = seedBuffer;
        this.hdKey = HDKey.fromMasterSeed(seedBuffer);
        // seedBuffer.entropy = seedTo(entity.mnemonic);
    }

    oneBySeed(fromAddress = 0, countChildKeys = 10) {
        let entity = {
            root: {
                privateExtendedKey: null,
                publicExtendedKey: null
            },
            child: {},
        };

        // entity.entropy = mnemonicToEntropy(entity.mnemonic);

        entity.root.privateExtendedKey = this.hdKey.privateExtendedKey;
        entity.root.publicExtendedKey = this.hdKey.publicExtendedKey;

        // let indexEntity = _.clone(this.indexEntityPattern);
        let indexEntity = ObjectHelper.clone(this.indexEntityPattern);


        indexEntity.address = fromAddress;
        // console.log(this.indexEntityPattern);

        entity.child = this.allChild(indexEntity, countChildKeys);
        return entity;
    }

    allChild(indexEntity, countChildKeys) {
        let childKeys = [];
        let fromAddress = indexEntity.address;
        for (let address = fromAddress; address < fromAddress + countChildKeys; address++) {
            indexEntity.address = address;
            childKeys.push(this.oneChild(indexEntity));
        }
        return childKeys;
    }

    oneChild(indexEntity) {
        let index = this._entityToIndex(indexEntity);

        let childKey = this.hdKey.derive(index);

        let childKeyItem = {
            index: null,
            address: null,
            publicKey: {
                extended: null,
                binary: null,
                hex: null,
                hash160: null,
                hash160Hex: null
            },
            privateKey: {
                extended: null,
                wif: null,
                wifEncrypted: null
            }
        };
        childKeyItem.index = index;

        // childKeyItem.privateExtendedKey = childKey.privateExtendedKey;
        // childKeyItem.publicExtendedKey = childKey.publicExtendedKey;

        childKeyItem.address = CryptoKeyHelper.addressByPublicKey(childKey.publicKey);

        // childKeyItem.publicKey = {};
        childKeyItem.publicKey.extended = childKey.publicExtendedKey;
        childKeyItem.publicKey.binary = childKey.publicKey;
        childKeyItem.publicKey.hex = childKey.publicKey.toString('hex');
        childKeyItem.publicKey.hash160 = CryptoKeyHelper.hashByPublicKey(childKey.publicKey);
        childKeyItem.publicKey.hash160Hex = CryptoKeyHelper.hashByPublicKey(childKey.publicKey).toString('hex');

        // childKeyItem.privateKey = {};
        childKeyItem.privateKey.extended = childKey.privateExtendedKey;
        childKeyItem.privateKey.wif = CryptoKeyHelper.privateKeyToWif(childKey.privateKey, true);
        if (this.encrypt.isEnabled) {
            childKeyItem.privateKey.wifEncrypted = CryptoKeyHelper.encryptPrivateKey(childKey.privateKey, this.encrypt.password);
        }
        return childKeyItem;
    }

    protected _entityToIndex(indexEntity) {
        return (
            "m/" +
            indexEntity.purpose + "'/" +
            indexEntity.coin + "'/" +
            indexEntity.account + "'/" +
            indexEntity.chain + "/" +
            indexEntity.address
        );
    }
}
