import container from "../../../../../../packages/core/container/singletons/container";
import RestorePasswordRepository from "../repositories/rpc/RestorePasswordRepository";
import RestorePasswordStorageRepository from "../repositories/localStorage/RestorePasswordRepository";

export default class RestorePasswordService {

    protected rpcRepository;
    protected storageRepository;

    constructor(rpcRepository: RestorePasswordRepository, storageRepository: RestorePasswordStorageRepository) {
        this.rpcRepository = rpcRepository;
        this.storageRepository = storageRepository;
    }

    async requestActivationCode(form) {
        try {
            await this.rpcRepository.requestActivationCode(form);
            this.storageRepository.setRequestForm(form);
        } catch (error) {
            throw error;
        }
    }

    async createPassword(form) {
        try {
            await this.rpcRepository.createPassword(form);
            this.storageRepository.setRequestForm(null);
        } catch (error) {
            throw error;
        }
    }

    getRequestForm() {
        return this.storageRepository.getRequestForm();
    }
}
