import container from "../../../../../packages/core/container/singletons/container";
import DocumentService from "../../../../../packages/bundles/crypt/services/DocumentService";
import VerifyEntity from "../../../signature/domain/entities/VerifyEntity";
import BaseP2pController from "../base/BaseP2pController";
import MessengerController from "../../../messenger/p2p/controllers/MessengerController";
import DocumentFormat from "../../../signature/domain/libs/bitcoin/DocumentFormat";
import cryptoFactory from "../../../signature/domain/factories/cryptoFactory";
import DocumentTypeEnum from "../enums/DocumentTypeEnum";
import SessionService from "../../../../../packages/bundles/e2ee/domain/services/SessionService";
import JsonBinaryEncoder from "../../../../../packages/ext/jsonBinary/libs/encoders/JsonBinaryEncoder";

export default class P2pHandler {

    protected routesConfig() {
        return {
            routes: {
                /*'handShake.start2': {
                    controller: 'e2ee.controllers.handShakeController',
                    action: 'start2',
                }*/
            },
            controllers: {
                // 'handShake': 'e2ee.controllers.handShakeController',
                'handShake': container.get('e2ee.controllers.handShakeController'),
                'messenger': new MessengerController(),
            },
        };
    }

    handle(cryptoRequest) {

        let documentFormat: DocumentFormat = cryptoFactory.createBitcoinDocumentFormat();
        let documentType = documentFormat.getType(cryptoRequest.document);

        if(documentType === DocumentTypeEnum.SIGNED) {
            let {address, request} = this.parseDocument(cryptoRequest.document);
            let route = request['method'];
            let {controllerDefinition, actionMethodName} = this.getRouteHandler(route);
            let actionInstance = this.getActionInstance(controllerDefinition, actionMethodName, address);
            return actionInstance(address, request);
        } else if(documentType === DocumentTypeEnum.ENCRYPTED) {
            let decodedDocument = documentFormat.parse(cryptoRequest.document);
            let address = decodedDocument['from_address'];
            let sessionService: SessionService = container.get('e2ee.services.session');
            let sessionEntity = sessionService.get(address);
            console.log('sessionEntity11111111111111111', sessionEntity);
            // console.log(sessionEntity['sessionKeys']);
            console.log('decodedDocument', decodedDocument);
        }
    }

    private getRouteHandler(route) {
        let [controller, action] = route.split('.');
        let routesConfig = this.routesConfig();
        let actionMethodName;
        let controllerDefinition;
        let routeItem = routesConfig.routes[route] ? routesConfig.routes[route] : null;
        if (routeItem) {
            controllerDefinition = routeItem['controller'];
            actionMethodName = routeItem['action'];
        } else {
            controllerDefinition = routesConfig.controllers[controller] ? routesConfig.controllers[controller] : null;
            actionMethodName = action;
        }
        return {controllerDefinition, actionMethodName};
    }

    private getActionInstance(controllerDefinition, actionMethodName, companionAddress) {
        let controllerInstance: BaseP2pController = this.controllerDefinitionToInstance(controllerDefinition);
        let actionInstance = controllerInstance[actionMethodName];
        actionInstance = actionInstance.bind(controllerInstance);
        return actionInstance;
    }

    private controllerDefinitionToInstance(controllerDefinition): BaseP2pController {
        let controllerInstance;
        if (typeof controllerDefinition === 'string') {
            controllerInstance = container.get(controllerDefinition);
        } else if (typeof controllerDefinition === 'object') {
            controllerInstance = controllerDefinition;
        } else {
            throw new Error('Bad type of controller');
        }
        return controllerInstance;
    }

    private static verify(document): VerifyEntity {
        let documentService: DocumentService = container.get('crypt.services.document');
        let verifyEntity = documentService.verify(document);
        if (!verifyEntity.isVerified) {
            throw new Error('Bad signature!');
        }
        return verifyEntity;
    }

    private parseDocument(document) {
        let verifyEntity = P2pHandler.verify(document);
        let address = verifyEntity['signature']['address'];
        let messageJson = verifyEntity['signature']['message'];

        let encoder = new JsonBinaryEncoder();
        let request = encoder.decode(messageJson);
        // let request = JSON.parse(messageJson);

        return {
            address,
            request,
        };
    }
}
