import CoinKey from "coinkey";
import * as crypto from "crypto";
import secp256k1 from "secp256k1";
import Address from "btc-address";
import binConv from 'binstring';
import hash from 'crypto-hashing';
import HashAlgorithmEnum from "../../../../../packages/ext/hash/enums/HashAlgorithmEnum";
import HashHelper from "../../../../../packages/ext/hash/helpers/HashHelper";
import wif from "wif";
import bip38 from "bip38";
// import bip32 from "bip32";
import _ from "lodash";
import bs58 from "bs58";
import ConvHelper from "../../../../../packages/ext/binary/helpers/ConvHelper";
import CryptoHashHelper from "./CryptoHashHelper";

export default class CryptoKeyHelper {

    /*static addressHashByPublicKey(pubKey) {
        let keyHash = hash(HashAlgorithmEnum.SHA256, pubKey);
        return hash(HashAlgorithmEnum.RIPEMD160, keyHash);
    }*/


// compressed public key from X and Y
    static generateCompressedPublicKeyFromXY(x, y) {
        const pubKey = new Uint8Array(33);
        pubKey[0] = (y[31] & 1) === 0 ? 0x02 : 0x03;
        pubKey.set(x, 1);
        return pubKey
    }

    static hashByPublicKey(pubKey) {
        return CryptoHashHelper.sha256Ripemd160(pubKey);
    }

    static addressByPublicKey(pubKey) {
        let keyHash = CryptoHashHelper.sha256Ripemd160(pubKey);
        let address = new Address(binConv(keyHash, { in : 'hex', out: 'bytes'}));
        return address.toString();
    }

    static addressByHash(pubKey) {
        let keyHash = CryptoHashHelper.sha256Ripemd160(pubKey);
        var address = new Address(binConv(keyHash, { in : 'hex', out: 'bytes'}));
        return address.toString();
    }

    static isCompressedPrivateKey(wifKey) {
        let ck = this.forgeCoinKeyFromPrivateKey(wifKey);
        return ck.compressed;
    }

    static forgeEntity(privateKeyWif) {

        let entity = {};
        entity["privateKey"] = this.wifPrivateKeyToBinary(privateKeyWif);
        // entity.isCompressed = this.isCompressedPrivateKey(entity.privateKey);
        entity["privateKeyWif"] = this.privateKeyToWif(entity["privateKey"], true);

        // entity["privateKeyWif"] = privateKeyWif;

        entity["publicKey"] = this.extractPublicKey(entity["privateKey"], true/*, entity.isCompressed*/);
        entity["publicHash"] = this.hashByPublicKey(entity["publicKey"]);
        entity["address"] = this.addressByPublicKey(entity["publicKey"]);
        // console.log(entity);
        return entity;
    }

    static extractPublicKeyFromWif(privateKeyWif, compressed = undefined, versions = undefined) {
        //let isCompressed = this.isCompressedPrivateKey(privateKeyWif);

        if(compressed === undefined) {
            compressed = this.isCompressedPrivateKey(privateKeyWif);
        }

        let privateKeyBinary = this.wifPrivateKeyToBinary(privateKeyWif);
        return this.extractPublicKey(privateKeyBinary, compressed);
    }

    static extractPublicKey(privKey: any, compressed = undefined, versions = undefined) {
        // todo:    compressed
        // isCompressed = CryptoKeyHelper.isCompressedPrivateKey(privateKeyWif);
        let privateKeyBuf;

        if(compressed === undefined) {
            compressed = this.isCompressedPrivateKey(privKey);
        }

        if(privKey instanceof Buffer) {
            privateKeyBuf = privKey;
        } else {
            privateKeyBuf = new Buffer(privKey);
        }
        let ck = new CoinKey(privateKeyBuf, compressed);
        return ck.publicKey;
    }

    static privateKeyToWif(privKey, compressed = undefined) {
        if(compressed === undefined) {
            compressed = this.isCompressedPrivateKey(privKey);
        }
        if(typeof privKey === 'object') {
            /*if(privKey instanceof Uint8Array) {
                privKey = new Buffer(privKey);
            }
            let ck = new CoinKey(privKey, compressed);*/
            let ck = this.forgeCoinKeyFromPrivateKey(privKey);
            return ck.privateWif;
        }
        if(_.isString(privKey)) {
            return privKey;
        }
    }

    static encryptPrivateKey(privKey, passphrase) {
        let decoded = wif.decode(this.privateKeyToWif(privKey));
        return bip38.encrypt(decoded.privateKey, decoded.compressed, passphrase);
    }

    static decryptPrivateKey(encryptedKeyWif, passphrase) {
        let isEncrypted = this.isEncrypted(encryptedKeyWif);
        if(isEncrypted) {
            if(_.isEmpty(passphrase)) {
                throw new Error('Enter passphrase');
            }
            let decryptedKey = bip38.decrypt(encryptedKeyWif, passphrase, function (status) {
                console.log(status.percent) // will print the percent every time current increases by 1000
            });
            return decryptedKey.privateKey;
        } else {
            return this.wifPrivateKeyToBinary(encryptedKeyWif);
        }
    }

    static isEncrypted(wifKey) {
        let decoded = bs58.decode(wifKey);
        // var ck = CoinKey.fromWif(wifKey);
        //const restored = bip32.fromBase58(wifKey);
        // let privateKeyBuf = this.wifPrivateKeyToBinary(wifKey);

        let buf = new Buffer(decoded);
        return buf[0] === 1 && buf[1] === 66;

        // console.log(buf[0], buf[1]);
    }


    static forgeCoinKeyFromPrivateKey(privKey, compressed = undefined): CoinKey {
        let ck: CoinKey;
        if(typeof privKey === 'object') {
            if(privKey instanceof Uint8Array) {
                privKey = new Buffer(privKey);
            }
            ck = new CoinKey(privKey);
        } else if(typeof privKey === 'string') {
            ck = CoinKey.fromWif(privKey);
        }
        return ck;
    }


    static binaryPrivateKeyToWif(privKey, compressed = undefined) {
        if(!(privKey instanceof Buffer)) {
            privKey = ConvHelper.toBuffer(privKey);
        }
        if(compressed === undefined) {
            compressed = this.isCompressedPrivateKey(privKey);
        }
        let ck = new CoinKey(privKey, compressed);
        return ck.privateWif;
    }

    static wifPrivateKeyToBinary(wif) {
        var ck = CoinKey.fromWif(wif);
        return ck.privateKey;
    }

    static generatePrivateKey() {
        let privKey = null;
        do {
            privKey = crypto.randomBytes(32);
        } while (!secp256k1.privateKeyVerify(privKey));
        return privKey;
    }
}
