import _ from "lodash";
import userProviderFacade from "../../../security/domain/singletons/userProviderFacade";

export default class RbacProviderService {

    isCan(permission): boolean {
        let tokenEntity = userProviderFacade.getTokenEntity();
        let myPermissions = tokenEntity.getIdentity()['permissions'];
        if (!myPermissions) {
            return true;
        }
        return myPermissions.includes(permission);
    }

    isMyRole(roleName) {
        let roleNames;
        if (Array.isArray(roleName)) {
            roleNames = roleName;
        } else if (_.isString(roleName)) {
            roleNames = [roleName];
        }
        return this.isMyRoles(roleNames);
    }

    protected isMyRoles(roleNames) {
        let tokenEntity = userProviderFacade.getTokenEntity();
        if (!tokenEntity.isAuthenticated()) {
            return false;
        }
        if (_.isEmpty(tokenEntity.getIdentity()["roles"])) {
            return false;
        }
        let intersectRoles = _.intersection(roleNames, tokenEntity.getIdentity()["roles"]);
        return !_.isEmpty(intersectRoles);
    }
}
