import HashHelper from "../../../../../packages/ext/hash/helpers/HashHelper";

export default class CryptoHashHelper {

    static sha256x2(value) {
        let hash = HashHelper.sha256(value);
        return HashHelper.sha256(hash);
    }

    static sha256Ripemd160(value) {
        let hash = HashHelper.sha256(value);
        return HashHelper.ripemd160(hash);
    }
}
