import diConfigurator from "../../../../../../packages/core/container/singletons/diConfigurator";
import RegistrationApiRepository from "../repositories/rpc/RegistrationApiRepository";
import RegistrationRequestFormRepository from "../repositories/localStorage/RegistrationRequestFormRepository";
import RegistrationService from "../services/RegistrationService";

export default function configureDi(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    bundleDiConfigurator.singleton("repositories.api.registration", RegistrationApiRepository);
    bundleDiConfigurator.singleton("repositories.storage.registration", RegistrationRequestFormRepository);
    bundleDiConfigurator.bind(
        "services.registration",
        RegistrationService,
        [
            "this.repositories.api.registration",
            'registration.repositories.storage.registration'
        ]
    );
}
