import diConfigurator from "../../../../../../packages/core/container/singletons/diConfigurator";
import AuthRpcRepository from "../repositories/rpc/AuthRepository";
import AuthService from "../services/AuthService";

export default function configureDi(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    bundleDiConfigurator.singleton("repositories.api.auth", AuthRpcRepository);
    bundleDiConfigurator.bind(
        "services.auth",
        AuthService,
        [
            "this.repositories.api.auth",
        ]
    );
}
