
import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import KeyRepository from "../../../key/domain/repositories/localStorage/KeyRepository";
import cryptoFactory from "../factories/cryptoFactory";
import SignerService from "../services/SignerService";

export default function configureDi(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);
    bundleDiConfigurator.bind("services.signer", () => {
        let keyRepository = new KeyRepository();
        let messageSigner = cryptoFactory.createBitcoinMessageSigner();
        return new SignerService(keyRepository, messageSigner);
    });
}
