
import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import ChatApiRepository from "../repositories/rpc/ChatRepository";
import MessageApiRepository from "../repositories/rpc/MessageRepository";
import ChatStoreRepository from "../repositories/state/ChatRepository";
import MessageStoreRepository from "../repositories/state/MessageRepository";
import ChatService from "../services/ChatService";
import MessageService from "../services/MessageService";

export default function configureDi(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    bundleDiConfigurator.singleton("repositories.api.chat", ChatApiRepository);
    bundleDiConfigurator.singleton("repositories.api.message", MessageApiRepository);
    bundleDiConfigurator.singleton("repositories.state.chat", ChatStoreRepository);
    bundleDiConfigurator.singleton("repositories.state.message", MessageStoreRepository);

    bundleDiConfigurator.bind(
        "services.chat",
        ChatService,
        [
            "this.repositories.state.chat",
            "this.repositories.api.chat",
        ]
    );
    bundleDiConfigurator.bind(
        "services.message",
        MessageService,
        [
            "this.repositories.state.message",
            "this.repositories.api.message",
        ]
    );
}
