
import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import BaseCrudRpcRepository from "../../../../../packages/bundles/rpc/repositories/base/BaseCrudRpcRepository";
import BaseCrudService from "../../../../../packages/core/domain/services/BaseCrudService";

export default function configureDi(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    bundleDiConfigurator.bind("repositories.api.company", () => {
        let repo = new BaseCrudRpcRepository();
        repo.methodPrefix = 'companyCompany';
        return repo;
    });
    bundleDiConfigurator.bind(
        "services.company",
        (apiRepository) => new BaseCrudService(null, apiRepository),
        [
            "this.repositories.api.company"
        ]
    );

    bundleDiConfigurator.bind("repositories.api.employee", () => {
        let repo = new BaseCrudRpcRepository();
        repo.methodPrefix = 'companyEmployee';
        return repo;
    });
    bundleDiConfigurator.bind(
        "services.employee",
        (apiRepository) => new BaseCrudService(null, apiRepository),
        [
            "this.repositories.api.employee"
        ]
    );
}
