import eventEmitter from "../../../../packages/core/event/singletons/eventEmitter";
import container from "../../../../packages/core/container/singletons/container";
import RpcEventEnum from "../../../../packages/bundles/rpc/enums/RpcEventEnum";
import UnprocessableEntityError from "../../../../packages/core/contract/errors/UnprocessableEntityError";
import toastFacade from "../../../../packages/bundles/notify/domain/facades/toastFacade";
import UnauthorizedError from "../../../../packages/core/contract/errors/UnauthorizedError";
import errorHelper from "../../../../packages/bundles/rpc/libs/errorHelper";
import _ from "lodash";
import LocaleHelper from "../../../../packages/bundles/language/domain/helpers/LocaleHelper";
import "../../../../packages/bundles/rpc/config/eventDev";
import ConnectionRepository from "../../../../packages/ext/app/repositories/config/ConnectionRepository";
import AppEventEnum from "../../domain/enums/AppEventEnum";
import UrlHelper from "../../../../packages/ext/html/helpers/UrlHelper";
import configManager from "../../../../packages/core/configManager/singletons/configManager";

eventEmitter.on(AppEventEnum.AFTER_BOOTSTRAP_LOAD, function () {
    let connectionEntity = ConnectionRepository.first();
    container.get('rpc.repositories.api.transport').rpcUrl = connectionEntity.rpcUrl;
});

eventEmitter.on(RpcEventEnum.CLIENT_RESPONSE_ERROR, function (error) {
    if (error instanceof UnprocessableEntityError) {
        toastFacade.error(errorHelper.unprocessableEntityErrorToString(error));
    } else if (error instanceof UnauthorizedError) {
        toastFacade.info(error.message);
        // let loginRoute = config.user.loginRoute;
        let loginRoute = configManager.get('user.loginRoute');
        window.location.href = UrlHelper.normalize(loginRoute);
    } else {
        toastFacade.error(error.message);
    }
});

//    RPC-
eventEmitter.on(RpcEventEnum.CLIENT_BEFORE_SEND, function (requestEntity) {
    let tokenEntity = container.get('security.services.userProvider').getTokenEntity();
    if (tokenEntity.isAuthenticated()) {
        _.set(requestEntity, 'meta.Authorization', tokenEntity.getToken());
    }
});

//    RPC-
eventEmitter.on(RpcEventEnum.CLIENT_BEFORE_SEND, function (requestEntity) {
    let languageEntity = container.get('language.services.switch').oneSelected();
    if (languageEntity && languageEntity.code) {
        let locale = LocaleHelper.encode(languageEntity.code);
        _.set(requestEntity, 'meta.Language', locale);
    }
});

