import container from "../../../../../packages/core/container/singletons/container";
import CryptoKeyHelper from "../helpers/CryptoKeyHelper";
import hash, {sha256} from "crypto-hashing";
import Address from "btc-address";
import conv from 'binstring';

export default class KeyService {

    protected keyRepository;

    constructor(keyRepository) {
        this.keyRepository = keyRepository;
    }

    getPublicKey() {
        let keyEntity = this.keyRepository.one();
        return keyEntity.publicKey;
        // let privateKeyBinary = CryptoKeyHelper.wifPrivateKeyToBinary(privateKey);
        // let publicKeyBinary = CryptoKeyHelper.extractPublicKey(privateKeyBinary);
        // return publicKeyBinary;
    }

    getAddress() {
        let publicKeyBinary = this.getPublicKey();
        return CryptoKeyHelper.addressByPublicKey(publicKeyBinary);
        // let pubKeyHash = hash.ripemd160(sha256(publicKeyBinary));
        // let address = new Address(conv(pubKeyHash, {in: 'hex', out: 'bytes'}));
        // return address.toString();
    }

    one() {
        return this.keyRepository.one();
    }

    savePrivateKey(privateKey) {
        let privateKeyBinary = CryptoKeyHelper.wifPrivateKeyToBinary(privateKey);
        this.keyRepository.savePrivateKey(privateKey);
    }
}
