
import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import KeyRepository from "../repositories/localStorage/KeyRepository";
import HdKeyRepository from "../repositories/localStorage/HdKeyRepository";
import DocumentRepository from "../../../../../packages/bundles/crypt/repositories/rpc/DocumentRepository";
import KeyService from "../services/KeyService";

export default function configureDi(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    let keyRepository = new KeyRepository();

    bundleDiConfigurator.bind("repositories.storage.hdKey", HdKeyRepository);
    bundleDiConfigurator.bind("repositories.api.document", DocumentRepository);
    bundleDiConfigurator.bind("services.key", () => new KeyService(keyRepository));
}
