import LoginInterface from '../../interfaces/LoginInterface';
import _ from "lodash";
import EncodeInterface from "../../../../../../../packages/core/contract/encoders/EncodeInterface";

export default class BaseLoginKey implements LoginInterface {

    protected hasher: EncodeInterface;

    public setHasher(hasher: EncodeInterface): void {
        this.hasher = hasher;
    }

    public all(login: string, password: string): object {
        let entity = {};
        entity['entropy'] = this.generateEntropyByLogin(login, password);
        entity['password'] = this.generatePasswordHash(login, password);
        return entity;
    }

    public generateEntropyByLogin(login: string, password: string) {
        let scope = [
            'entropy',
            login,
            // password,
        ];
        let value = this.prepareValue(scope);
        return this.hasher.encode(value);
    }

    public generatePasswordHash(login: string, password: string) {
        let scope = [
            'password',
            password,
            login,
        ];
        let value = this.prepareValue(scope);
        return this.hasher.encode(value);
    }

    protected prepareValue(value: string[]): string {
        for (let i in value) {
            if (value.hasOwnProperty(i)) {
                value[i] = _.trim(value[i]);
            }
        }
        return value.join("|");
    }
}
