import eventEmitter from "../../../../packages/core/event/singletons/eventEmitter";
import toastFacade from "../../../../packages/bundles/notify/domain/facades/toastFacade";
import {t} from "i18next";
import SecurityEventEnum from "../../../../packages/bundles/user/security/domain/enums/SecurityEventEnum";

eventEmitter.on(SecurityEventEnum.LOGIN, function (token) {
    toastFacade.success(t('authorization.message.signInSuccess'));
});

eventEmitter.on(SecurityEventEnum.LOGOUT, function (token) {
    toastFacade.success(t('authorization.message.signOutSuccess'));
});
