import DocumentFormat from "../libs/bitcoin/DocumentFormat";
import BitcoinSigner from "../libs/bitcoin/BitcoinSigner";
import MessageSigner from "../libs/MessageSigner";
import CoinMsg from "../libs/CoinMsg";

class CryptoFactory {

    createBitcoinMessageSigner(privateKey = null) {
        let signer = this.createBitcoinSigner();
        if(privateKey) {
            signer.privateKey = privateKey;
        }
// let signer = new Signer();
        return new MessageSigner(signer);
    }

    createBitcoinSigner() {
        let documentFormat = this.createBitcoinDocumentFormat();
        return new BitcoinSigner(documentFormat);
    }

    createBitcoinDocumentFormat() {
        return new DocumentFormat('BITCOIN');
    }

    createBitcoinCoinMsg() {
        let options = {
            coinname: 'BITCOIN',
            curve: 'secp256k1',
            magicPrefix: '\x18Bitcoin Signed Message:\n'
        };
        return new CoinMsg(options);
    }
}

export default new CryptoFactory();
