import container from "../../../../../packages/core/container/singletons/container";

export default class SignerService {

    protected keyRepository;
    protected messageSigner;

    constructor(keyRepository, messageSigner) {
        this.keyRepository = keyRepository;
        this.messageSigner = messageSigner;
    }

    sign(message) {
        let keyEntity = container.get('key.services.key').one();
        if (!keyEntity.privateKey) {
            new Error('Not found private key!');
        }
        return this.messageSigner.sign(message, keyEntity.privateKey);
    }

    verify(signature, message, pubKey = undefined) {
        return this.messageSigner.verify(signature, message, pubKey);
    }

    recoverPublicKey(signature, msgHash) {
        return this.messageSigner.recoverPublicKey(signature, msgHash);
    }
}
