
var version = 'coinmsg/v0.1.3';

module.exports = function(coinname, address, sig, message) {
  Buffer.isBuffer(sig) && (sig = sig.toString('base64'));

  return [
    '-----BEGIN '+coinname+' SIGNED MESSAGE-----',
    message,
    '-----BEGIN '+coinname+' SIGNATURE-----',
    'Version: ' + version,
    'Address: ' + address,
    sig,
    '-----END '+coinname+' SIGNATURE-----'
  ].join('\n')
};
