import Pbkdf2Hasher from "../../../../../../packages/ext/hash/libs/encoders/hash/Pbkdf2Hasher";
import EncodeInterface from "../../../../../../packages/core/contract/encoders/EncodeInterface";

export default class Pbkdf2ChainHash implements EncodeInterface {

    protected _cost: number;
    protected _salt = 'salt';
    protected _keyLengthBytes: number = 32;

    constructor(cost: number) {
        if (cost < 1) {
            throw new Error('Cost not can be less then 1');
        }
        this._cost = cost;
    }

    get salt() {
        return this._salt;
    }

    set salt(value) {
        if (value.length < 3) {
            throw new Error('Salt length not can be less then 3');
        }
        this._salt = value;
    }

    get keyLengthBytes(): number {
        return this._keyLengthBytes;
    }

    set keyLengthBytes(value: number) {
        this._keyLengthBytes = value;
    }

    encode(sourceValue) {
        let key = sourceValue;
        let iterations = this._cost;
        let hasher = new Pbkdf2Hasher(this._salt, iterations, this._keyLengthBytes);
        return hasher.encode(key);
    }
}
