import socketEventEnum from "../../../../../packages/bundles/webSocket/enums/socketEventEnum";
import MessengerEventEnum from "../enums/MessengerEventEnum";
import React from 'react';
import {Link} from "react-router-dom";
import MessageForm from "../../web/components/messageForm";
import EventEmitter from "events";
import Container from "../../../../../packages/core/container/libs/Container";
import eventEmitter from "../../../../../packages/core/event/singletons/eventEmitter";
import container from "../../../../../packages/core/container/singletons/container";
import eventConfigurator from "../../../../../packages/core/event/singletons/eventConfigurator";
import toastFacade from "../../../../../packages/bundles/notify/domain/facades/toastFacade";

eventConfigurator.register(function (container: Container, eventEmitter: EventEmitter) {
    eventEmitter.on(socketEventEnum.MESSAGE, function (socketEventEntity) {
        if (socketEventEntity.name === 'sendMessage') {
            container.get('messenger.services.message').newMessage(socketEventEntity.data);
        }
    });

    eventEmitter.on(MessengerEventEnum.NEW_MESSAGE, function (data) {
        let chatId = container.get('messenger.services.message').getChatId();
        if (data.direction === 'in' && chatId !== data.chatId) {
            const Msg = ({closeToast, toastProps}) => (
                <div>
                    New message!
                    <br/>
                    {data.text}
                    <br/>
                    <a className="btn btn-xs btn-primary" href={"#/messenger/" + data.chatId}>
                        Go
                    </a>
                    {/*@ts-ignore*/}
                    <MessageForm chatId={data.chatId}/>
                </div>
            );
            toastFacade.info(Msg);
        }
    });
});
