import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import SummaryRepository from "../repositories/array/SummaryRepository";
import SummaryService from "../services/SummaryService";

export default function configureDi(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    bundleDiConfigurator.singleton("repositories.api.summary", SummaryRepository);
    bundleDiConfigurator.bind(
        "services.summary",
        // NotificationService,
        (summaryApiRepository) => new SummaryService(null, summaryApiRepository),
        [
            "this.repositories.api.summary",
        ]
    );
}
