import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import reduceConfigurator from "../../../../../packages/core/state/singletons/reduceConfigurator";

export default function configureReducer(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    reduceConfigurator.bindFromRepositoryId(bundleDiConfigurator.get("repositories.state.category"));
}
