import P2pService from "../../../../../packages/bundles/e2ee/domain/services/P2pService";
import P2pRequestEntity from "../../../../../packages/bundles/e2ee/domain/entities/P2pRequestEntity";
import TimeHelper from "../../../../../packages/ext/time/helpers/TimeHelper";

export default class MessageService {

    private p2pService: P2pService;

    constructor(p2pService: P2pService) {
        this.p2pService = p2pService;
    }

    send(address: string, content: string) {
        let messageRequest = new P2pRequestEntity('messenger.sendMessage', {
            message: content,
            createdAt: TimeHelper.timestamp(),
        });
        this.p2pService.sendRequest(address, messageRequest);
    }
}
