import container from "../../../../../../packages/core/container/singletons/container";
import MnemonicHelper from "../../../../key/domain/helpers/MnemonicHelper";
import TokenHelper from "../helpers/TokenHelper";
import CryptoKeyHelper from "../../../../key/domain/helpers/CryptoKeyHelper";
import ConvHelper from "../../../../../../packages/ext/binary/helpers/ConvHelper";
import BaseLoginKey from "../libs/loginKey/BaseLoginKey";
import TimeHelper from "../../../../../../packages/ext/time/helpers/TimeHelper";
import TokenEntity from "../../../../../../packages/bundles/user/security/domain/entities/token/TokenEntity";
import Base94Encoder from "../../../../../../packages/ext/baseX/libs/encoders/Base94Encoder";
import ConnectionRepository from "../../../../../../packages/ext/app/repositories/config/ConnectionRepository";

export default class AuthService {

    protected loginKey: BaseLoginKey;

    constructor(loginKey: BaseLoginKey) {
        this.loginKey = loginKey;
    }

    getRpcToken() {
        let connectionEntity = ConnectionRepository.first();
        // let connectionEntity = server.connections[0];
        let parsedUrl = new URL(connectionEntity.webSocketUrl);
        let json = JSON.stringify({
            hostname: parsedUrl.hostname,
            timestamp: TimeHelper.timestamp(),
        });
        let sig = container.get('signature.services.signer').sign(json);
        return 'crypto ' + ConvHelper.toHex(sig.document);
    }

    seedByLogin(login: string, password: string) {
        let entropyBinary = this.loginKey.generateEntropyByLogin(login, password);
        let passwordBinary = this.loginKey.generatePasswordHash(login, password);


        let base94Encoder = new Base94Encoder();
        let passwordBase94 = base94Encoder.encode(passwordBinary);

        // let passwordBase94 = BaseXHelper.toBase94(passwordBinary);
        return MnemonicHelper.entropyToSeed(entropyBinary, passwordBase94);
    }

    authByLogin(login: string, password: string) {
        let seedBuffer = this.seedByLogin(login, password);
        return this.authBySeed(seedBuffer);
    }

    authByMnemonic(mnemonic: string, password: string) {
        let seedBuffer = MnemonicHelper.mnemonicToSeed(mnemonic, password);
        return this.authBySeed(seedBuffer);
    }

    authBySeed(seedBuffer) {
        let wif = MnemonicHelper.seedHexToPrivateKeyWif(seedBuffer);
        this.authByPrivateKeyWif(wif);
    }

    authByPrivateKeyWif(privateKeyWif: string, password: string = null) {
        let decryptedKeyBinary = CryptoKeyHelper.decryptPrivateKey(privateKeyWif, password);
        privateKeyWif = CryptoKeyHelper.binaryPrivateKeyToWif(decryptedKeyBinary);
        let keyService = container.get('key.services.key');
        keyService.savePrivateKey(privateKeyWif);
        let keyEntity = keyService.one();
        // let tokenEntity = tokenHelper.forgeTokenEntity(keyEntity);
        let identityEntity = TokenHelper._forgeIdentityEntity(keyEntity);
        let tokenString = this.getRpcToken();
        let tokenEntity = new TokenEntity(tokenString, identityEntity);
        container.get('security.services.userProvider').login(tokenEntity);
        return tokenEntity;
    }
}
