export default class BitcoinSigner {

    protected _privateKey;
    protected _publicKey;
    protected _documentFormat;
    protected _hashAlgorithm = HashAlgorithmEnum.SHA256;

    constructor(documentFormat: DocumentFormat) {
        this._documentFormat = documentFormat;
    }

    set privateKey(value) {
        this._privateKey = value;
        this._publicKey = CryptoKeyHelper.extractPublicKey(value);
    }

    hash(message) {
        return HashHelper.hash(this._hashAlgorithm, message);
    }

    sign(msgHash, privateKey) {
        /*privateKey = privateKey ? privateKey : this._privateKey;
        let signatureEntity = new SignatureEntity();
        signatureEntity.signature = coinmsg.sign(privateKey, message, true);
        signatureEntity.publicKey = CryptoKeyHelper.extractPublicKey(privateKey);
        signatureEntity.messageHash = msgHash;
        return signatureEntity;*/
    }

    signMessage(message, privateKey) {
        privateKey = privateKey ? privateKey : this._privateKey;
        let signatureEntity = new SignatureEntity();
        signatureEntity.signature = coinmsg.sign(privateKey, message, true);
        let isCompressed = true;
        // let isCompressed = CryptoKeyHelper.isCompressedPrivateKey(privateKey);
        signatureEntity.publicKey = CryptoKeyHelper.extractPublicKey(privateKey, isCompressed);
        signatureEntity.address = CryptoKeyHelper.addressByPublicKey(signatureEntity.publicKey);
        signatureEntity.messageHash = this.hash(message);
        signatureEntity.document = this._documentFormat.encode(CryptoKeyHelper.addressByPublicKey(signatureEntity.publicKey), signatureEntity.signature, message);
        return signatureEntity;
    }

    recoverPublicKey(signature, message) {
        let coinmsgFork = cryptoFactory.createBitcoinCoinMsg();
        return coinmsgFork.recoverPubKey(signature, message);


        /*const sigObj = this.parseSignature(signature);
        // console.log(signature, sigObj)
        sigObj.recid = 1;
        return secp256k1.ecdsaRecover(sigObj.signature, sigObj.recid, msgHash);*/
    }

    verify(signature, msgHash, pubKey = null) {



        const sigObj = this.parseSignature(signature);
        let pubKeyRecovered = secp256k1.ecdsaRecover(sigObj.signature, sigObj.recid, msgHash);
        if(pubKey && Buffer.from(pubKey).toString('hex') !== Buffer.from(pubKeyRecovered).toString('hex')) {
            return false;
        }
        let isVerified = secp256k1.ecdsaVerify(sigObj.signature, msgHash, pubKey);
        return isVerified;
        /*if (!isVerified) {
            return false;
        }

        return pubKey;*/
    }



    verifyMessage(signature, message, pubKey = null) {

        if(!pubKey) {
            pubKey = this.recoverPublicKey(signature, message);
        }

        let coinmsgFork = cryptoFactory.createBitcoinCoinMsg();
        let isVerify = coinmsgFork.verify(pubKey, ConvHelper.toBuffer(signature), message);
        return isVerify;

        // let address = CryptoKeyHelper.addressByPublicKey(pubKey);
        // let isVerified = coinmsg.verify(address, signature, message);
        //
        //
        // /*const sigObj = this.parseSignature(signature);
        // let pubKeyRecovered = secp256k1.ecdsaRecover(sigObj.signature, sigObj.recid, msgHash);
        // if(pubKey && Buffer.from(pubKey).toString('hex') !== Buffer.from(pubKeyRecovered).toString('hex')) {
        //     return false;
        // }
        // let isVerified = secp256k1.ecdsaVerify(sigObj.signature, msgHash, pubKey);*/
        // return isVerified;
        // /*if (!isVerified) {
        //     return false;
        // }
        //
        // return pubKey;*/
    }

    verifyEntity(signatureEntity: SignatureEntity) {
        if(!signatureEntity.publicKey) {
            signatureEntity.publicKey = this.recoverPublicKey(signatureEntity.signature, signatureEntity.message);
        }

        let verifyEntity = new VerifyEntity();

        let coinmsgFork = cryptoFactory.createBitcoinCoinMsg();
        verifyEntity.isVerified = coinmsgFork.verify(signatureEntity.publicKey, signatureEntity.signature, signatureEntity.message);
        verifyEntity.signature = signatureEntity;
        verifyEntity.publicKey = signatureEntity.publicKey;

        return verifyEntity;
    }

    parseSignature(signature) {
        return {
            signature: signature.subarray(0, 64),
            recid: signature[64],
        };
    }

    serializeSignatureObject(sigObj) {
        let array = Array.from(sigObj.signature);
        array.push(sigObj.recid);
        // @ts-ignore
        return Uint8Array.from(array);
    }
}
