import eventEmitter from "../../../../packages/core/event/singletons/eventEmitter";
import container from "../../../../packages/core/container/singletons/container";
import SecurityEventEnum from "../../../../packages/bundles/user/security/domain/enums/SecurityEventEnum";
import "../../../../packages/bundles/webSocket/config/eventDev";
import socketEventEnum from "../../../../packages/bundles/webSocket/enums/socketEventEnum";
import toastFacade from "../../../../packages/bundles/notify/domain/facades/toastFacade";
import ConnectionRepository from "../../../../packages/ext/app/repositories/config/ConnectionRepository";
import TokenEntityInterface from "../../../../packages/bundles/user/security/domain/interfaces/TokenEntityInterface";
import Timer from "../../../../packages/ext/cron/libs/Timer";
import configManager from "../../../../packages/core/configManager/singletons/configManager";

eventEmitter.on(SecurityEventEnum.SET_TOKEN_ENTITY, function (tokenEntity: TokenEntityInterface) {
    let webSocketConnection = container.get('webSocket.services.connection');
    let isEnable = configManager.get('webSocket.enable', false);
    if (tokenEntity.isAuthenticated() && isEnable) {
        // console.log(tokenEntity.getToken());
        let callback = () => {
            let connectionEntity = ConnectionRepository.first();
            webSocketConnection.url = connectionEntity.webSocketUrl;
            webSocketConnection.open();
        };
        let startDelay = configManager.get('webSocket.startDelay', 1000);
        let timer = new Timer();
        timer.start(startDelay, callback);
    } else {
        webSocketConnection.close();
    }
});

eventEmitter.on(socketEventEnum.BREAK, function (event) {
    toastFacade.error("WebSocket.  . : " + event.code);
});

eventEmitter.on(SecurityEventEnum.LOGOUT, function () {
    let webSocketConnection = container.get('webSocket.services.connection');
    webSocketConnection.close();
});

eventEmitter.on(socketEventEnum.ERROR, function (error) {
    toastFacade.error("WebSocket.  " + error.message);
});
