import BaseRpcRepository from "../../../../../../../packages/bundles/rpc/repositories/base/BaseRpcRepository";

export default class RegistrationApiRepository extends BaseRpcRepository {

    async requestActivationCode(body) {
        let requestEntity = {
            method: 'userRegistration.requestActivationCode',
            body: body,
        };

        try {
            let responseEntity = await this.sendRequest(requestEntity);
            return responseEntity.body;
        } catch (error) {
            throw error;
        }
    }

    async createAccount(body) {
        let requestEntity = {
            method: 'userRegistration.createAccount',
            body: body,
        };

        try {
            let responseEntity = await this.sendRequest(requestEntity);
            return responseEntity.body;
        } catch (error) {
            throw error;
        }
    }
}
