import BaseLocalStorageRepository from "../../../../../../packages/ext/permanentStorage/base/BaseLocalStorageRepository";
import CryptoKeyHelper from "../../helpers/CryptoKeyHelper";

export default class HdKeyRepository extends BaseLocalStorageRepository {

    key() {
        return 'cryptHdKey';
    }

    savePrivateKey(privateKey, password) {
        this.set('privateKey', privateKey);
    }

    one() {
        let privateKey = this.get('privateKey');
        return CryptoKeyHelper.forgeEntity(privateKey);
    }

    __forgeEntity2(entity) {
        entity.isCompressed = CryptoKeyHelper.isCompressedPrivateKey(entity.privateKey);
        entity.privateKey = CryptoKeyHelper.wifPrivateKeyToBinary(entity.privateKey);
        entity.publicKey = CryptoKeyHelper.extractPublicKey(entity.privateKey, entity.isCompressed);
        entity.publicHash = CryptoKeyHelper.hashByPublicKey(entity.publicKey);
        entity.address = CryptoKeyHelper.addressByPublicKey(entity.publicKey);
        // console.log(entity);
    }
}
