import BaseCrudRpcRepository from "../../../../../../packages/bundles/rpc/repositories/base/BaseCrudRpcRepository";
import DataProvider from "../../../../../../packages/core/domain/libs/DataProvider";
import Query from "../../../../../../packages/core/domain/libs/Query";

export default class OrderRepository extends BaseCrudRpcRepository {

    constructor() {
        super();
        this.methodPrefix = 'shopOrder';
    }

    async kanban(query: Query = null) {
        let requestEntity = {
            method: this.methodName('kanban'),
            // body: body,
        };
        this._forgeRequestByQuery(requestEntity, query);
        try {
            let responseEntity = await this.sendRequest(requestEntity);
            let dataProvider = new DataProvider();
            dataProvider.collection = responseEntity.body;
            dataProvider.paginator = this._createPaginatorFromRequestMeta(responseEntity.meta);
            dataProvider.query = query;
            return dataProvider;
        } catch (error) {
            throw error;
        }
    }
}
