import BaseRpcRepository from "../../../../../../../packages/bundles/rpc/repositories/base/BaseRpcRepository";

export default class RestorePasswordRepository extends BaseRpcRepository {

    async requestActivationCode(body) {
        let requestEntity = {
            method: 'restorePassword.requestActivationCode',
            body: body,
        };

        try {
            let responseEntity = await this.sendRequest(requestEntity);
            return responseEntity.body;
        } catch (error) {
            throw error;
        }
    }

    async createPassword(body) {
        let requestEntity = {
            method: 'restorePassword.createPassword',
            body: body,
        };

        try {
            let responseEntity = await this.sendRequest(requestEntity);
            return responseEntity.body;
        } catch (error) {
            throw error;
        }
    }
}
