import EnumLabelInterface from "../../../core/enum/interfaces/EnumLabelInterface";

export default class HashAlgorithmEnum implements EnumLabelInterface {

    static readonly SHA1 = 'sha1';
    static readonly SHA224 = 'sha224';
    static readonly SHA256 = 'sha256';
    static readonly SHA512 = 'sha512';
    static readonly SHA384 = 'sha384';
    static readonly SHA3 = 'sha3';
    static readonly RIPEMD160 = 'ripemd160';
    static readonly MD5 = 'md5';

    getLabels(): object {
        let labels = {};
        labels[HashAlgorithmEnum.SHA1] = 'SHA 1';
        labels[HashAlgorithmEnum.SHA224] = 'SHA 224';
        labels[HashAlgorithmEnum.SHA256] = 'SHA 256';
        labels[HashAlgorithmEnum.SHA512] = 'SHA 512';
        labels[HashAlgorithmEnum.SHA384] = 'SHA 384';
        labels[HashAlgorithmEnum.SHA3] = 'SHA 3';
        labels[HashAlgorithmEnum.RIPEMD160] = 'RIPEMD 160';
        labels[HashAlgorithmEnum.MD5] = 'MD 5';
        return labels;
    }
}