import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import CategoryRpcRepository from "../repositories/rpc/CategoryRepository";
import ProductRpcRepository from "../repositories/rpc/ProductRepository";
import CategoryStateRepository from "../repositories/state/CategoryRepository";
import CategoryService from "../services/CategoryService";
import ProductService from "../services/ProductService";
import BaseCrudRpcRepository from "../../../../../packages/bundles/rpc/repositories/base/BaseCrudRpcRepository";
import BaseCrudService from "../../../../../packages/core/domain/services/BaseCrudService";
import OrderRepository from "../repositories/rpc/OrderRepository";
import OrderService from "../services/OrderService";

export default function configureDi(domainName) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    bundleDiConfigurator.bind("repositories.api.category", CategoryRpcRepository);
    bundleDiConfigurator.bind("repositories.api.product", ProductRpcRepository);
    bundleDiConfigurator.bind("repositories.state.category", CategoryStateRepository);

    bundleDiConfigurator.bind(
        "services.category",
        CategoryService,
        [
            "this.repositories.state.category",
            "this.repositories.api.category",
        ]
    );
    bundleDiConfigurator.bind(
        "services.product",
        (productRepository) => new ProductService(null, productRepository),
        [
            "this.repositories.api.product"
        ]
    );


    bundleDiConfigurator.bind("repositories.api.order", () => {
        let repo = new OrderRepository();
        repo.methodPrefix = 'shopOrder';
        return repo;
    });
    bundleDiConfigurator.bind(
        "services.order",
        (apiRepository) => new OrderService(null, apiRepository),
        [
            "this.repositories.api.order"
        ]
    );

}
